@import url(https://fonts.googleapis.com/css2?family=Commissioner:wght@100;200;300;400;500;600;700&display=swap);
* {
  padding: 0;
  margin: 0;
  max-width: 100vw;
}

::-webkit-scrollbar{
  display: none;
}
.App{
    width: 100vw;
    margin: 0;
    font-family: 'Commisioner', sans-serif;
}

body{
    font-family: 'Commisioner', sans-serif;
}

.bg{
    /* background: url(./images/image-hero-mobile.jpg) no-repeat;
    background-size: cover;
