.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(black, transparent);
    position: absolute;
    z-index: 100;
    width: 90%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 5vh 5vw;
}
 


.hamburger > .burger{
    margin-right: 50vw;
}

.hamburger > .burger > img {
    width: 50px;
    height: 30px;
}

.nav-hide{
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    top: 5vh;
    left: 0;
}

.nav-hide > .close {
    align-self: flex-end;
    margin-right: 5vw;
}

.nav-hide > .close  img {
    -webkit-filter: invert(99%) sepia(10%) saturate(739%) hue-rotate(252deg) brightness(122%) contrast(100%);
            filter: invert(99%) sepia(10%) saturate(739%) hue-rotate(252deg) brightness(122%) contrast(100%);
}

.nav-nav {
    margin: 3vh 5vw 0 5vw;
    background: #ffffff;
    border-radius: 10px;
    align-items: center;
}

.nav-nav > h4 {
    margin: 5vw 5vh;
}

.nav-nav > hr {
    opacity: 45%;
}

.desktop-nav {
    display: none;
}

@media all and (min-width: 600px) {

    .nav {
        display: flex;
        justify-content: flex-start;
    }

    .nav-icon{
        margin-left: 5vw;
    }

    .hamburger , .nav-hide >.close {
        display: none;
    }

    .desktop-nav {
        display: flex;
        position: absolute;
        top: 5.3vh;
        right: 10vw;
    }

    .desktop-nav > ul{
        display: flex;
        justify-content: space-around;
        align-self: center;
    }

    .desktop-nav > ul > li {
        cursor: pointer;
        text-decoration: none;
        list-style: none;
        margin-left: 3vw;
        color: white;
        transition: 0.5s;
        opacity: 70%;
    }

    .desktop-nav > ul > li:hover {
        transform: scale(1.2);
        opacity: 100%;
    }
}





:root{
    --default: hsl(176, 50%, 47%);
    --inactive: hsl(0, 0%, 48%);
}

.btn{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 15px 35px;
    margin: 0;
    border-radius: 35px;
    border: hidden;
    font-family: Commisioner;
    font-size: 0.75rem;
    
}
 


/* Making design for button template */
.btn-nav{
    background: none;
    border: hidden;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    padding: 0;
    margin: 0;
}

.btn-nav > p{
    padding: 0;
}


.btn-option-nav{
    border: hidden;
    background: none;
}

.btn-default{
    background: hsl(176, 50%, 47%);
    background: var(--default);
    color: white;
    font-size: 16px;
}

.btn-default1{
    background: hsl(176, 50%, 47%);
    background: var(--default);
    color: white;
    font-size: 16px;
    padding: 0;
}

.btn-default1 > p{
    padding: 15px 35px;
}

.btn-bookmark{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding: 0;
    border-radius: 30px;
    background: none;
}

.btn-inactive{
    background: hsl(0, 0%, 48%);
    background: var(--inactive);
    font-size: 16px;
    color: #fff;
}


.modal {
    font-size: 12px;
    top: -55vh;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    padding: 0vh 5vw ; 
  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    width: 40vw;
    position: absolute;
    padding: 0;
    right: -25vw;
    top: 5vh;
  }

@media all and (min-width: 600px) {
  .modal {
    padding: 0vh 25vw ; 
  }

  .modal > .close {
    width: 40vw;
    position: absolute;
    padding: 0;
    right: -10vw;
    top: 5vh;
  }
}
.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: auto;
    height: auto;
    padding: 5vh 5vw;
    background: #fff;
    border: 1px solid rgba(99, 97, 97, 0.671);
    border-radius: 14px;
    text-align: center;
}


.about{
    position: relative;
    text-align: left ;
} 

.about > h3 {
    margin-bottom: 20px;
    font-size: 1rem;
}

.about > p {
    font-size: 0.75rem;
    line-height: 1rem;
    margin-bottom: 20px;
}

.reward-cards {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5vh;
}
.reward{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    text-align: left;
}

.pledge-text{
    color: hsl(176, 50%, 47%);
}

.reward-title > h3{
    font-size: 0.8rem;
    font-weight: 800;
    margin-bottom: 10px;
}

.pledge-text > h4{
    font-size: 0.75rem;
    font-weight: 650;
    margin-bottom: 20px;
}

.description{
    margin-bottom: 20px;
    font-size: 0.75rem;
    line-height: 1rem;
    white-space: unset;
}

.left > span {
    align-items: center;
    font-size: 1.8rem;
    font-weight: 800; 
}

.reward > .btn {
    justify-self: start;
    align-self: flex-start;
}

.pledge{
    text-align: left;
    line-height: 1.3rem;
}

.pledge-title > h3 {
    margin-bottom: 10px;
}

.pledge-title > p {
    margin-bottom: 15px;
}

.pledgecard-section  {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5vh;
    justify-content: center;
}

.home-card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: 100%;
    top: 40vh;
}


.home-card > .logo{
    position: absolute;
    left: 40vw;
    top: -2px;
}

.home-card > .title {
    padding: 0 6vw;
    font-size: 1rem;
    margin-top: 20px;
    margin-bottom: 15px;
}

.detail{
    margin-bottom: 5vh;
}

.detail > span {
    font-size: 0.8rem;
}

.btn-section{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btn-section  img {
    width: 8vh;
    height: 100%;
}

@media all and (min-width: 600px) {
    .home-card > .logo{
        position: absolute;
        left: 50vw;
        top: -2px;
    }
}
.pledge-card{
    align-self: center;
    text-align: left;
    width: 100%;
}

.pledge-card > .card-show > .pledgecard-title  {
    display: inline-flex;
    align-items: center;
    margin-bottom: 20px;
}

.pledge-card > .card-show > .pledgecard-title > .checkbox > label > input {
    padding: 20px;
    width: 20px;
    margin-right: 15px;
    border-radius: 10px;
}

.pledge-card > .card-show > .pledgecard-title > .title-detail > p {
    color: hsl(176, 50%, 47%);
}

.pledge-hide{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

hr {
    margin-top: 5px;
}

.pledge-hide > .title {
    margin: 13px 0px;
}

.pledge-card > .pledge-hide > .pledge {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pledge-hide > .pledge > .label > h4 {
    font-size: 18px;
    font-weight: 400;
}   

.pledge-hide > .pledge > .label {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    border: 1px solid rgba(78, 78, 78, 0.185);
    padding: 14px 0px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    border-radius: 45px;
}

.pledge-hide > .pledge > .label > label > input {
    width: 8vw;
    margin-left: 10px;
    border: hidden;
}



.pledge-card:focus > .pledge-hide{
    display: block;
}

.thank-card{
    margin: 0 5vw;
    font-size: 0.75rem;
}

.thank-title{
    margin: 5vh 0;
}

.thank-description {
    line-height: 1rem;
    margin-bottom: 5vh;
}

.dashboard{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cash{
    text-align: center;
}

.dashboard > .dashboard-details > .pledge-amount hr {
    align-self: center;
    margin-bottom: 20px;
    /* width: 75px; */
}

.dashboard > .dashboard-details > .donors > hr {
    align-self: center;
    margin-bottom: 20px;
    /* width: 75px; */
}

.cash > h3  {
    font-size: 2.2rem;
    font-weight: 650;
    margin-bottom: 20px ; 
}

.cash > p {
    font-size: 1rem;
    margin-bottom: 20px;
}

@media all and (min-width: 600px) {
    .dashboard > .dashboard-details{
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin-bottom: 15px;
    }

    .dashboard > .dashboard-details >  hr {
        display: none;
        align-self: center;
        margin-bottom: 20px;
        width: 75px;
    }

    .dashboard > .dashboard-details > .pledge-amount > hr {
        display: none;
    }

    .dashboard > .dashboard-details > .donors > hr {
        display: none;
    }

    .dashboard > .dashboard-details > .pledge-amount  {
        align-self: flex-start;
    }

    .dashboard > .dashboard-details > .donors {
        margin: 0 5vw;
        padding: 0 2vw;
        border-left: 1px solid rgba(71, 70, 70, 0.37);
        border-right: 1px solid rgba(71, 70, 70, 0.37);
    }
}


.home{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5vh;
    position: absolute;
    z-index: 10;
    top: 40vh;
    padding: 5vh 5vw 5vh 5vw;
    
}

@media all and (min-width: 375px) {
    .home{
        padding: 4vh 25vw ;
    }

}
