.about{
    position: relative;
    text-align: left ;
} 

.about > h3 {
    margin-bottom: 20px;
    font-size: 1rem;
}

.about > p {
    font-size: 0.75rem;
    line-height: 1rem;
    margin-bottom: 20px;
}

.reward-cards {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 5vh;
}